<template>
  <div class="search-container">
    <form class="form-inline">
      <div class="input-group input-width-100">
        <input 
          v-model="searchContent" 
          class="form-control" 
          type="search" 
          placeholder="" 
          aria-label="Search" 
          @input.prevent="showMatch" 
          @keydown.prevent="clickToMatch"
        >
        <div class="input-group-append">
          <button 
            id="button-addon2" 
            class="btn btn-outline-warning" 
            type="button" 
            @click.prevent="clickToMatch" 
          >
            <i class="fas fa-search"/>
          </button>
        </div>
      </div>
    </form> 
    <div 
      v-show="searchContent"
      class="search-result" 
    >
      <table class="table mb-1">
        <thead>
          <tr class="border-0">
            <th 
              class="text-muted" 
              colspan="2"
            >
              
            </th>
          </tr>
        </thead>
        <tbody v-if="searchContent">
          <tr 
            v-for="result in searchMatch" 
            :key="result.id" 
            class="search-result-item border-0" 
            @click.prevent="toMatchDetail(result.id)"
          >
            <td> <b>{{ result.title }}</b> </td>
            <td class="text-warning text-right">{{ result.price | currency }} </td>
          </tr>
        </tbody>
        <tbody v-if="searchMatch.length === 0">
          <tr class="border-0">
            <td 
              class="text-danger"
              colspan="2" 
            >
              
            </td>
          </tr>
        </tbody>                            
      </table>
    </div>
  </div>                
</template>


<script>
import $ from 'jquery';
import {mapActions,mapGetters} from 'vuex';
export default {
  data(){
    return{
      searchMatch:[], // match
      searchContent:'', // input
    }      
  },
  computed:{
    ...mapGetters('productsModules',['allProducts']),
  },
  created(){
    const vm = this;
    // vm.getAllProduct();
  },     
  methods:{
    getMatch(){
      const vm =this;
      vm.searchMatch = vm.allProducts.filter(item=> item.title.match(vm.searchContent));
    },  
    showMatch(){ // list
      const vm =this;
      if(vm.searchContent === ''){
        $('.search-result').hide();
      }
      else{
        $('.search-result').show();
        vm.getMatch();
      }
    },
    toMatchDetail(id){  
      const vm =this ;
      if (vm.$route.path === `/product_detail/${id}`) { // input
        vm.searchContent = '';
        return;
      }
      vm.$store.dispatch('updateLoading',true);// 
      vm.$router.push(`/product_detail/${id}`);
      if (vm.$route.name === 'ProductDetail') { // 
        window.location.reload(); // vue
      }
      vm.searchContent = '';
      vm.$store.dispatch('updateLoading',false);
    },
    clickToMatch(){ // 
      const vm =this;
      let result = vm.allProducts.find(item => item.title === vm.searchContent );
      if(result){ // 
        if(vm.$route.path === `/product_detail/${result.id}`){ // input
          vm.searchContent = '';
          return;                    
        }
        vm.$store.dispatch('updateLoading',true);
        vm.$router.push(`/product_detail/${result.id}`); 
        if (vm.$route.name === 'ProductDetail') { // 
          window.location.reload(); // vue
        }
        vm.searchContent = '';
        vm.$store.dispatch('updateLoading',false);                 
      }
    }             
  }, 
}
</script>

