<template>
  <section class="container">
    <Processbar process = "fillInfo"/>
    <div class="my-5 row justify-content-center">
      <div class="col-lg-6 col-md-8">
        <section 
          id="accordionExample" 
          class="accordion card-header-shadow border-0 mb-3" 
        >
          <div 
            v-if="carts.length === 0" 
            class="alert process-bg-danger text-center alert-rounded" 
            role="alert"
          >
            <router-link 
              to="/products" 
              class="text-white"
            >
              
            </router-link>
          </div>
          <div 
            v-if="carts.length > 0"  
            class="card bg-secondary" 
          >
            <div 
              id="headingOne"
              class="card-header border bg-primary" 
            >
              <h2 class="mb-0 ">
                <button 
                  class="btn btn-link text-white" 
                  type="button" 
                  data-toggle="collapse"
                  data-target="#collapseOne"
                  aria-expanded="true"
                  aria-controls="collapseOne"
                >
                  
                  <i class="fas fa-hand-point-left text-warning ml-1 fa-lg"/>
                </button>
              </h2>
            </div>
            <!--  .show  -->
            <div 
              id="collapseOne" 
              class="collapse" 
              aria-labelledby="headingOne"
              data-parent="#accordionExample"
            >
              <div class="card-body bg-secondary">
                <table class="table table-responsive-md">
                  <thead>
                    <tr class="text-warning">
                      <th 
                        v-if="!isCoupon"
                        class="align-middle" 
                        width="60" 
                      >
                        
                      </th>
                      <th 
                        class="align-middle mobile-d-none" 
                        width="100"
                      >
                        
                      </th>
                      <th class="d-md-none"/>
                      <th class="align-middle"></th>
                      <th 
                        class="align-middle" 
                        width="100"
                      >
                        
                      </th>
                      <th 
                        class="align-middle" 
                        width="80"
                      >
                        
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr 
                      v-for="cart in carts" 
                      :key="cart.id" 
                      class="text-white"
                    >
                      <td 
                        v-if="!isCoupon"
                        class="align-middle" 
                      >
                        <button 
                          id="cartIn" 
                          class="btn btn-outline-danger" 
                          name="cartIn"
                          type="button" 
                          data-title="remove" 
                          data-toggle="modal"
                          data-target="#goodsCancel"
                          @click.prevent="removeCart(cart.id)" 
                        >
                          <i 
                            v-if="!status.loadingItem"
                            class="fas fa-trash-alt" 
                          />
                          <i 
                            v-if="status.loadingItem"
                            class="fas fa-spinner fa-spin" 
                          />
                        </button>
                      </td>
                      <td class="align-middle mobile-d-none">
                        <img 
                          :src="cart.product.imageUrl"
                          class="img-fluid"
                          alt=""
                        >
                      </td>
                      <td class="d-md-none"/>
                      <td class="align-middle">{{ cart.product.title }}</td>
                      <td class="align-middle">{{ cart.qty }}/{{ cart.product.unit }}</td>
                      <td class="align-middle text-right">{{ cart.total | currency }}</td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <!--  3000 -->
                    <tr 
                      v-if="cartsData.total<3000 & !isCoupon"
                      class="text-white" 
                    >
                      <td 
                        class="text-right"
                        colspan="4" 
                      >
                        <b></b>
                      </td>
                      <td class="text-right"><b>$ 60</b></td>
                    </tr>
                    <!--  3000header colspan=3 -->
                    <tr 
                      v-if="cartsData.total<3000 & isCoupon"
                      class="text-white" 
                    >
                      <td 
                        class="text-right"
                        colspan="3" 
                      >
                        <b></b>
                      </td>
                      <td class="text-right"><b>$ 60</b></td>
                    </tr>                                        
                    <tr 
                      v-if="cartsData.total>=3000"
                      class="text-white" 
                    >
                      <td 
                        v-if="!isCoupon"
                        class="text-right" 
                        colspan="4" 
                      >
                        <b></b>
                      </td>
                      <td 
                        v-if="isCoupon"
                        class="text-right" 
                        colspan="3" 
                      >
                        <b></b>
                      </td>
                      <td class="text-right"><del><b>$ 60</b></del></td>
                    </tr> 
                    <tr 
                      v-if="cartsData.total>=3000"
                      class="text-danger" 
                    >
                      <td 
                        v-if="!isCoupon"
                        class="text-right" 
                        colspan="4"
                      >
                        <b></b>
                      </td>
                      <td 
                        v-if="isCoupon"
                        class="text-right" 
                        colspan="3" 
                      >
                        <b></b>
                      </td>
                      <td 
                        v-if="!isCoupon"
                        class="text-right" 
                      >
                        <b>0</b>
                      </td>
                      <td 
                        v-if="isCoupon"
                        class="text-right" 
                      >
                        <b>{{ cartsData.total - cartsData.final_total |currency }}</b>
                      </td>
                    </tr> 
                    <!--  3000 -->                                                                               
                    <tr 
                      v-if="cartsData.total<3000 & !isCoupon"
                      class="text-danger" 
                    >
                      <td 
                        class="text-right" 
                        colspan="4" 
                      >
                        <b></b>
                      </td>
                      <td class="text-right"><b>{{ cartsData.total + 60 |currency }}</b></td>
                    </tr>
                    <!--  3000header colspan=3 -->
                    <tr 
                      v-if="cartsData.total<3000 & isCoupon"
                      class="text-danger" 
                    >
                      <td 
                        class="text-right" 
                        colspan="3" 
                      >
                        <b></b> 
                      </td>
                      <td class="text-right"><b>{{ cartsData.total + 60 |currency }}</b></td>
                    </tr>                                        
                    <tr 
                      v-if="cartsData.total>=3000"
                      class="text-danger" 
                    >
                      <td 
                        v-if="!isCoupon"
                        class="text-right" 
                        colspan="4" 
                      >
                        <b></b> 
                      </td>
                      <td 
                        v-if="isCoupon"
                        class="text-right" 
                        colspan="3" 
                      >
                        <b></b> 
                      </td>
                      <td 
                        v-if="!isCoupon"
                        class="text-right" 
                      >
                        <b>{{ cartsData.total |currency }}</b>
                      </td>
                      <td 
                        v-if="isCoupon"
                        class="text-right" 
                      >
                        <b>{{ cartsData.final_total |currency }}</b>
                      </td>
                    </tr>   
                  </tfoot>
                </table>
              </div>
            </div>
            <!--  -->
            <div 
              v-if="cartsData.total<3000"
              class="alert bg-warning text-center alert-rounded text-white mt-3" 
              role="alert" 
            >
              {{ 3000-cartsData.total |currency }}
            </div>
            <div 
              v-if="cartsData.total>=3000 & !isCoupon"
              class="alert bg-dark text-center alert-rounded text-danger mt-3 font-weight-bold" 
              role="alert" 
            >
              
            </div>
            <div 
              v-if="cartsData.total>=3000 & isCoupon"
              class="alert bg-success text-center alert-rounded text-white mt-3 font-weight-bold" 
              role="alert" 
            >
              
            </div>
            <router-link 
              to="/products" 
              class="btn btn-danger mt-2" 
            >
              
            </router-link >
          </div>
        </section>  
        <div 
          v-if="cartsData.total>=3000 & !isCoupon" 
          class="input-group mb-3" 
        >
          <input 
            v-model="coupon_code" 
            class="form-control" 
            type="text" 
            placeholder="" 
            aria-label="Recipient's username" 
            aria-describedby="button-addon2" 
          >
          <div class="input-group-append">
            <button 
              id="button-addon2" 
              class="btn btn-outline-warning" 
              type="button" 
              @click="addCouponCode" 
            >
              
            </button>
          </div>
        </div>
        <!--  -->
        <form 
          v-if="carts.length > 0"
          class="text-white" 
          @submit.prevent="createOrder" 
        >
          <div class="form-group">
            <label for="username"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
            <input 
              v-validate="'required'" 
              id="username"
              v-model="form.user.name"  
              :class="{'is-invalid':errors.has('name')}"
              class="form-control" 
              type="text" 
              name="name" 
              placeholder=""
            >
            <span 
              v-if="errors.has('name')"
              class="text-danger" 
            >
              
            </span>
          </div>

          <div class="form-group">
            <label for="useremail"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
            <input 
              v-validate="'required|email'" 
              id="useremail"
              v-model="form.user.email"  
              :class="{'is-invalid':errors.has('email')}"
              class="form-control" 
              type="email" 
              name="email" 
              placeholder=" Email" 
            >
            <span 
              v-if="errors.has('email')"
              class="text-danger" 
            >
              {{ errors.first('email') }}
            </span>
          </div>    

          <div class="form-group">
            <label for="usertel"><span class="h5"></span> <span class="text-danger"><b>()</b></span></label>
            <input 
              v-validate="'required'" 
              id="usertel" 
              v-model="form.user.tel" 
              :class="{'is-invalid':errors.has('tel')}" 
              class="form-control" 
              name="tel" 
              placeholder="" 
              type="tel"
            >
            <span 
              v-if="errors.has('tel')"
              class="text-danger"
            >
              
            </span>
          </div>

          <div class="form-group">
            <label for="useraddress"><span class="h5"></span><span class="text-danger"><b>()</b></span></label>
            <input 
              v-validate="'required'" 
              id="useraddress" 
              v-model="form.user.address"
              :class="{'is-invalid':errors.has('address')}" 
              class="form-control" 
              type="text" 
              name="address" 
              placeholder=""
            >
            <span 
              v-if="errors.has('address')" 
              class="text-danger" 
            >
              
            </span>
          </div>

          <div class="form-group">
            <label for="comment"><span class="h5"></span></label>
            <textarea 
              id="comment" 
              v-model="form.message" 
              class="form-control" 
              cols="30" 
              rows="10" 
              placeholder=":"
            />
          </div>
          <div class="text-right">
            <button class="btn btn-primary"></button>
          </div>
        </form>
      </div>    
    </div> 
    <!-- 3000 modal  -->
    <div 
      id="eventAlert" 
      class="modal mt-5" 
      tabindex="-1" 
      role="dialog" 
    >
      <div 
        class="modal-dialog "
        role="document"
      >
        <div class="modal-content ">
          <div class="modal-header bg-danger ">
            <h5 class="modal-title text-white ">3000</h5>
            <button 
              type="button" 
              class="close" 
              data-dismiss="modal" 
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body text-right">
            <button 
              type="button"
              class="btn btn-outline-secondary mr-3" 
              data-dismiss="modal"
            >
              
            </button>
            <button 
              type="button"
              class="btn btn-primary"
              @click.prevent="toEvent"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>    
    <!-- 3000 modal  -->
    <div 
      id="warningAlert" 
      class="modal mt-5" 
      tabindex="-1" 
      role="dialog" 
    >
      <div 
        class="modal-dialog " 
        role="document"
      >
        <div class="modal-content ">
          <div class="modal-header bg-danger ">
            <h5 class="modal-title text-white ">3000</h5>
            <button 
              type="button" 
              class="close" 
              data-dismiss="modal" 
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body text-right">
            <button 
              type="button"
              class="btn btn-outline-secondary mr-3" 
              data-dismiss="modal">
              
            </button>
            <button 
              type="button" 
              class="btn btn-primary" 
              @click.prevent="toProducts"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>             
  </section>
</template>

<script>
import Processbar from '../../../components/Frontdesk/Processbar';
import $ from 'jquery';
import {mapActions,mapGetters} from 'vuex';


export default {
    components:{
        Processbar
    },
    data(){
        return{
            form:{ //
                user: {
                    name: '',
                    email:'' ,
                    tel: '',
                    address: ''
                },
                message:''
            }, 
            coupon_code:'' , //    
            isCoupon : false, // 
        }
    },
    computed:{
        ...mapGetters('cartsModules',['carts','cartsData','status','isdisabled']),
    },
    created(){
      const vm =this;
      vm.getCart();
    },     
    methods:{
        ...mapActions('cartsModules',['getCart','removeCart']),
        createOrder(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            const api = `${apiPath}/api/${customPath}/order`; // Api , message , user 
            if( !vm.isCoupon & vm.$store.state.cartsModules.cartsData.total >=3000 ){ //3000 
                $('#eventAlert').modal('show');
                   return
            }
            else if( vm.isCoupon & vm.$store.state.cartsModules.cartsData.total < 3000){ // 3000 
                $('#warningAlert').modal('show');
                return
            } 
            vm.$store.dispatch('updateLoading',true); // vuexupdateLoading if else cli2.03.0 loading
            const order = vm.form            
            this.$validator.validate().then((result) => { // vee-validate
                if (result) {
                    this.$http.post(api,{data:order}).then((response)=>{
                        vm.$store.dispatch('cartsModules/getCart');
                        vm.$store.dispatch('updateLoading',false);
                        vm.$router.push(`/check_out/checkOrder/${response.data.orderId}`) //
                    })  
                }
                else{
                    // alert('');
                    vm.$store.dispatch('updateMessage',{
                        message:'',
                        status:'danger', 
                        });
                    vm.$store.dispatch('updateLoading',false);    
                }
            }); 
        },
        addCouponCode(){
            const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
            const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
            const vm =this ;
            const coupon = {code:vm.coupon_code};
            vm.$store.dispatch('updateLoading',true);
            const api = `${apiPath}/api/${customPath}/coupon`; // Api 
            this.$http.post(api,{data:coupon}).then((response)=>{
                if(response.data.success){
                    vm.getCart(); //
                    vm.$store.dispatch('cartsModules/getCart');
                    vm.isCoupon = true; 
                    vm.$store.dispatch('updateLoading',false);
                    vm.$store.dispatch('updateMessage',{
                        message:'',
                        status:'success', 
                        });                    
                }
                else{
                    vm.$bus.$emit('message:push',response.data.message ,'danger');
                    vm.$$store.dispatch('updateMessage',{
                        message:response.data.message,
                        status:'danger', 
                    });                    
                }

            })              
        },
        toEvent(){ // modal  modaldisabled
            const vm =this;
            $('#eventAlert').modal('hide');
            vm.$router.push('/event');
        } ,
        toProducts(){ 
            const vm =this;
            $('#warningAlert').modal('hide');
            vm.$router.push('/products');
        },                                   
    }, 
}
</script>

<style lang="css">
@media(max-width: 767px){
    .mobile-d-none{
        display: none;
    }
}


    
</style>