<template>
  <section class="container">
    <!-- <loading :active.sync="isLoading" ></loading> -->
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb bg-dark" >
        <li class="breadcrumb-item">
          <router-link 
            to="/products" 
            class="text-white"
          >
            
          </router-link>
        </li>
        <li class="breadcrumb-item">
          <!-- <a href="#" class="text-white">Library</a> -->
          <router-link 
            :to="{ path: '/products', query: { category: product.category } }"
            class="text-white" 
          >
            {{ product.category }}
          </router-link>                
        </li>
        <li 
          class="breadcrumb-item active" 
          aria-current="page"
        >
          {{ product.title }}
        </li>
      </ol>
    </nav>      
    <div class="row my-4">
      <div class="col-md-6">
        <div 
          :style="{backgroundImage:`url('${product.imageUrl}')`}"
          class="bg-contain" 
        />
      </div>
      <div class="col-md-6 pl-md-5 mt-md-0 mt-4 text-md-left text-center text-white" >
        <h3 class="text-warning text-shadow-red" >{{ product.title }}</h3>
        <del 
          v-if="product.origin_price > 0"
          class="text-white" 
        >
          {{ product.origin_price | currency }}
        </del>
        <h4 
          v-if="product.origin_price > 0"
          class="text-danger font-weight-bold"
        >
          {{ product.price | currency }}
        </h4>
        <h4 
          v-if="product.origin_price == 0"
          class="text-danger font-weight-bold "
        >
          
        </h4>                
        <h3 
          v-if="product.is_enable"
          class="text-right" 
        >
          
        </h3>
        <h4 
          v-if="product.is_enable"
          class="text-right text-danger font-weight-bold" 
        >
          {{ product.price*product.num | currency }}
        </h4>
        <div v-if="product.is_enable">
          <label for="num"></label>
          <select 
            v-model="product.num" 
            class="form-control" 
            name="num"
          >
            <option 
              v-for="num in 10" 
              :value="num" 
              :key="num" 
            >
               {{ num }} {{ product.unit }} 
            </option>
          </select>  
          <button
            :disabled="isdisabled"
            class="btn btn-primary w-100 mt-3"
            type="button"
            @click="addtoCart(product.id,product.num)"
          >
            
          </button>                                      
        </div>
        <div class="mt-2">
          <h4 class="text-warning"></h4>
          <p>{{ product.description }}</p>
          <h4 class="text-warning"></h4>
          <p>{{ product.content }}</p>
        </div>
        <div v-if="!product.is_enable">
          <h3 class="text-danger" ></h3>
        </div>                
      </div>
    </div>  
  </section >
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  data(){
    return{
      product:{}, // id
      productId:'', // id
    }
  },
  computed:{
    ...mapGetters('cartsModules',['isdisabled','loadingItem'])
  },
  created(){
    const vm =this;
    vm.productId = vm.$route.params.productId; // ProductDetail id
    vm.getProduct() ; // id
  },   
  methods:{
    getProduct(){ //  id
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      vm.$store.dispatch('updateLoading',true);
      const api = `${apiPath}/api/${customPath}/product/${vm.productId}`; // idApi
      this.$http.get(api).then((response)=>{
        if(response.data.success){
          vm.product = response.data.product;
          vm.defaultNum(); //  product.num  1 
          vm.$store.dispatch('updateLoading',false);
        }
      }) 
    },
    defaultNum(){ //  product.num  1 
      const vm =this;
      vm.product.num = 1;
    },
    addtoCart(id,qty=1){
      this.$store.dispatch('cartsModules/addtoCart',{id,qty});
    },                
  },   
}
</script>


