import Vue from 'vue';
import VueRouter from 'vue-router';
// Dashboard
import Dashboard from '@/components/Dashboard/Dashboard' ;
import Login from '@/views/Dashboard/pages/Login' ; 
import Production from '@/views/Dashboard/pages/Production' ; //
import Order from '@/views/Dashboard/pages/Order' ; //
import CustomerOrders from '@/views/Dashboard/pages/CustomerOrders'; //
import CustomerCheck from '@/views/Dashboard/pages/CustomerCheck'; // 
import Ticket from '@/views/Dashboard/pages/Ticket' ; // 
// Frontdesk
import Frontdesk from '@/components/Frontdesk/Frontdesk';
import Index from '@/views/Frontdesk/pages/Index';
import Products from '@/views/Frontdesk/pages/Products';
import About from '@/views/Frontdesk/pages/About';
import Event from '@/views/Frontdesk/pages/Event';
import ProductDetail from '@/views/Frontdesk/pages/ProductDetail';
import CheckOut from '@/components/Frontdesk/CheckOut';
import CheckOrder from '@/views/Frontdesk/pages/CheckOrder';
import FillInfo from '@/views/Frontdesk/pages/FillInfo';

Vue.use(VueRouter) 

export default new VueRouter({
	linkActiveClass: 'active', // router-link class:'active'
	routes:[
		{
			path:'*', // 
			redirect:'/index', // 
		},
		{
			name:'Frontdesk',
			path:'/frontdesk',
			component:Frontdesk,
			children:[
				{
					name:'Index',
					path:'/index',
					component:Index,                    
				},
				{
					name:'Products',
					path:'/products',
					component:Products,
					props: route => ({ category: route.query.category }), // props
				},
				{
					name:'About',
					path:'/about',
					component:About
				},
				{
					name:'Event',
					path:'/event',
					component:Event
				},  
				{
					path: '/product_detail/:productId',
					name: 'ProductDetail',
					component: ProductDetail,
				},     
				{
					name:'CheckOut',
					path:'/check_out',
					component:CheckOut,
					children:[
						{
							name:'FillInfo',
							path:'fill_info',
							component:FillInfo
						},
						{
							name:'CheckOrder',
							path:'checkOrder/:orderId',
							component:CheckOrder,
						},
					]
				}                         
			]
		},
		{
			name:'Login',
			path:'/login',
			component: Login,
		},
		{
			name:'Dashboard', // 
			path:'/admin', //  
			component: Dashboard, //
			children:[
				{
					path:'production',
					name:'Production',
					component:Production,
					meta: { requiresAuth: true }, // 
				},
				{
					path:'order',
					name:'Order',
					component:Order,
					meta: { requiresAuth: true }, // 
				},
				{
					path:'ticket',
					name:'Ticket',
					component:Ticket,
					meta: { requiresAuth: true }, //                     
				}
			]
		},
		{ // Dashboard 
			name:'Dashboard_withoutValid', // 
			path:'/dashboard', //  
			component: Dashboard, //
			children:[
				{ // 
						path:'/customer_orders',
						name:'CustomerOrders',
						component: CustomerOrders,
				},
				{ // 
						path:'/customer_check/:orderId',
						name:'CustomerCheck',
						component: CustomerCheck,
						
				},                
			]
		},
	],
}) 




