<template>
  <div>
    <!-- <loading :active.sync="isLoading" ></loading>         -->
    <div class="row">
      <div 
        v-for="item in products" 
        :key="item.id"
        class="col-md-4 mb-4" 
      >
        <div class="card border-0 shadow-sm">
          <div 
            :style="{backgroundImage:`url(${item.imageUrl})`}"
            style="height: 350px; background-size: cover; background-position: center"
          />
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a 
                href="#" 
                class="text-dark"
              >
                {{ item.title }}
              </a>
            </h5>
            <p class="card-text">{{ item.content }}</p>
            <div class="d-flex justify-content-between align-items-baseline">
              <!-- <div class="h5">2,800 </div> -->
              <del class="h6"> {{ item.origin_price | currency }} </del>
              <div class="h5"> {{ item.price | currency }} </div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <button 
              class="btn btn-outline-secondary btn-sm" 
              type="button" 
              @click = "getProduct(item.id)"
            > 
              <i 
                v-if="status.loadingItem === item.id"
                class="fas fa-spinner fa-spin" 
              />
              
            </button>
            <button 
              class="btn btn-outline-danger btn-sm ml-auto"
              type="button" 
              @click="addtoCart(item.id)"
            >
              <i 
                v-if="status.loadingItem === item.id"
                class="fas fa-spinner fa-spin" 
              />
              
            </button>
          </div>
        </div>
      </div>            
    </div> 
    <Pagination  
      :pagination-prop="pagination"
      @emit-update-page="getProducts"
    />
    <hr>
    <!--  -->
    <div 
      v-if="carts.length !== 0"
      class="row d-flex justify-content-center"  
    >
      <div class="col-6">
        <table class="table" >
          <thead>
            <tr>
              <th width="80"/>
              <th></th>
              <th width="80"></th>
              <th width="100"></th>
            </tr>
          </thead>
          <tbody>
            <tr 
              v-for="cart in carts" 
              :key="cart.id"
            >
              <td>
                <button 
                  class="btn btn-outline-danger btn-sm" 
                  @click="removeCart(cart.id)"
                >
                  <i class="fas fa-trash-alt"/>
                </button>
              </td>
              <td>{{ cart.product.title }}</td>
              <td>{{ cart.qty }} / {{ cart.product.unit }} </td>
              <td class="text-right">{{ cart.product.price | currency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr class="font-weight-bold">
              <td 
                class="text-right"
                colspan="3" 
              ></td>
              <td class="text-right">{{ cartsData.total | currency }}</td>
            </tr>
            <tr 
              v-if="cartsData.final_total !== cartsData.total"
              class="text-success font-weight-bold" 
            >
              <td 
                class="text-right"
                colspan="3" 
              >
                
              </td>
              <td class="text-right">{{ cartsData.final_total | currency }}</td>
            </tr>
          </tfoot>
        </table>  
        <div class="input-group mb-3">
          <input 
            v-model="coupon_code" 
            class="form-control" 
            type="text" 
            placeholder="" 
            aria-label="Recipient's username" 
            aria-describedby="button-addon2"
          >
          <div class="input-group-append">
            <button 
              id="button-addon2" 
              class="btn btn-outline-secondary" 
              type="button" 
              @click="addCouponCode"
            >
              
            </button>
          </div>
        </div>                              
      </div>
    </div>
    <!--  -->
    <div class="my-5 row justify-content-center">
      <form 
        class="col-md-6" 
        @submit.prevent="createOrder"
      >
        <div class="form-group">
          <label for="useremail">Email <span class="text-danger">()</span></label>
          <input 
            v-validate="'required|email'" 
            id="useremail"
            :class="{'is-invalid':errors.has('email')}"
            v-model="form.user.email"  
            class="form-control" 
            placeholder=" Email" 
            type="email" 
            name="email" 
          >
          <span 
            v-if="errors.has('email')"
            class="text-danger" 
          >
            {{ errors.first('email') }}
          </span>    
        </div>
    
        <div class="form-group">
          <label for="username"> <span class="text-danger">()</span></label>
          <input 
            v-validate="'required'" 
            id="username"
            :class="{'is-invalid':errors.has('name')}"
            v-model="form.user.name"  
            class="form-control" 
            placeholder=""
            type="text" 
            name="name" 
          >
          <span 
            v-if="errors.has('name')"
            class="text-danger" 
          >
            
          </span>
        </div>
    
        <div class="form-group">
          <label for="usertel"> <span class="text-danger">()</span></label>
          <input 
            v-validate="'required'" 
            id="usertel" 
            :class="{'is-invalid':errors.has('tel')}" 
            v-model="form.user.tel" 
            class="form-control" 
            type="tel" 
            name="tel" 
            placeholder=""
          >
          <span 
            v-if="errors.has('tel')"
            class="text-danger"  
          >
            
          </span>
        </div>
    
        <div class="form-group">
          <label for="useraddress"> <span class="text-danger">()</span></label>
          <input 
            v-validate="'required'" 
            id="useraddress" 
            v-model="form.user.address"
            :class="{'is-invalid':errors.has('address')}" 
            class="form-control" 
            name="address" 
            type="text" 
            placeholder=""
          >
          <span 
            v-if="errors.has('address')"
            class="text-danger"  
          >
            
          </span>
        </div>
    
        <div class="form-group">
          <label for="comment"></label>
          <textarea 
            id="comment" 
            v-model="form.message" 
            class="form-control" 
            cols="30" 
            rows="10" 
            placeholder=":"
          />
        </div>
        <div class="text-right">
          <button class="btn btn-danger"></button>
        </div>
      </form>
    </div>        
    <!-- Modal -->
    <div
      id="productModal"
      class="modal fade"
      tabindex="-1"
      role="dialog"
      aria-labelledby="productModalLabel"
      aria-hidden="true"
    >
      <div 
        class="modal-dialog" 
        role="document"
      >
        <div class="modal-content">
          <div class="modal-header">
            <h5 
              id="productModalLabel"
              class="modal-title" 
            >
              {{ product.title }}
            </h5>
            <button 
              class="close" 
              type="button" 
              data-dismiss="modal" 
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img 
              :src="product.imageUrl" 
              class="img-fluid" 
            >
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div 
                v-if="!product.price"
                class="h5 ml-auto" 
              >
                {{ product.origin_price | currency }}
              </div>
              <del 
                v-if="product.price"
                class="h6" 
              >
                 {{ product.origin_price | currency }}
              </del>
              <div 
                v-if="product.price"
                class="h5" 
              >
                 {{ product.price | currency }}
              </div>
            </div>
            <select 
              v-model="product.num"
              class="form-control mt-3" 
            >
              <option 
                v-for="num in 10" 
                :value="num" 
                :key="num" 
              >
                 {{ num }} {{ product.unit }} 
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div 
              v-if="product.num>=1"
              class="text-muted text-nowrap mr-3" 
            >
              
              <strong>{{ product.num * product.price }}</strong> 
            </div>
            <button
              class="btn btn-primary"
              type="button"
              @click="addtoCart(product.id,product.num)"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>              
  </div>
</template>


<script>
import Pagination from '../../../components/Pagination' //Pagination 
import $ from 'jquery';

export default {
  components:{
    Pagination
  },   
  data:function(){
    return{
      products:[], //
      product:{}, //
      carts:[], //
      cartsData:{}, // 
      isLoading:false,
      status:{
        loadingItem:'' //idloading
      },
      coupon_code:'' , //model
      form:{ //
        user: {
          name: '',
          email:'' ,
          tel: '',
          address: ''
        },
        message:''
      },
      pagination:{}
    }
  },
  created(){
    this.getProducts();
    this.getCart();
  },     
  methods:{
    getProducts(page = 1){
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      vm.$store.dispatch('updateLoading',true);
      const api = `${apiPath}/api/${customPath}/products?page=${page}`; // Api 
      this.$http.get(api).then((response)=>{
        vm.products = response.data.products.sort(function(a,b){
          return a.category<b.category ? 1 : -1;
        })
        console.log('',vm.products);
        vm.$store.dispatch('updateLoading',false);
        vm.pagination = response.data.pagination;
      }) 
    },
    getProduct(id){ //  id
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      vm.status.loadingItem = id; // id
      const api = `${apiPath}/api/${customPath}/product/${id}`; // idApi
      this.$http.get(api).then((response)=>{
        vm.product = response.data.product;
        $('#productModal').modal('show');
        console.log('',vm.product);
        vm.status.loadingItem = ''; // modal vm.status.loadingItem loading
      }) 
    },
    addtoCart(id,qty = 1){ // modal id1
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      vm.status.loadingItem = id; // id
      const cart = {
        product_id:id, //id
        qty, // 
      };
      const api = `${apiPath}/api/${customPath}/cart`; // idApi
      this.$http.post(api,{data:cart}).then((response)=>{
        console.log('',response);
        vm.status.loadingItem = ''; // modal vm.status.loadingItem loading
        vm.getCart(); // 
        $('#productModal').modal('hide'); // modal
      }); 
    },
    getCart(){ // 
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      vm.$store.dispatch('updateLoading',true);
      const api = `${apiPath}/api/${customPath}/cart`; // Api 
      this.$http.get(api).then((response)=>{
        // console.log('',response);
        vm.carts = response.data.data.carts;
        vm.cartsData = response.data.data ;
        console.log('',vm.carts);
        vm.$store.dispatch('updateLoading',false);
      })             
    },
    removeCart(id){ // id
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      vm.$store.dispatch('updateLoading',true);
      const api = `${apiPath}/api/${customPath}/cart/${id}`; // idApi 
      this.$http.delete(api).then((response)=>{
        console.log('',response);
        vm.getCart(); //
        vm.$store.dispatch('updateLoading',false);
      })           
    },
    addCouponCode(){
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      const coupon = {code:vm.coupon_code};
      vm.$store.dispatch('updateLoading',true);
      const api = `${apiPath}/api/${customPath}/coupon`; // Api 
      this.$http.post(api,{data:coupon}).then((response)=>{
        console.log('',response);
        vm.getCart(); //
        vm.$store.dispatch('updateLoading',false);
      })              
    },
    createOrder(){
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      const api = `${apiPath}/api/${customPath}/order`; // Api , message , user 
      vm.$store.dispatch('updateLoading',true);
      const order = vm.form
      this.$validator.validate().then((result) => { // vee-validate
        if (result) {
          this.$http.post(api,{data:order}).then((response)=>{
            console.log('',response);
            vm.$store.dispatch('updateLoading',false);
            vm.$router.push(`/customer_check/${response.data.orderId}`) //
          })  
        }
        else{
          alert('');
        }
      }); 
    }       
  },
}
</script>