<template>
  <div id="app">
    <loading :active.sync="isLoading"/>
    <router-view/>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'

export default {
  name: 'App',
  computed:{
    ...mapGetters(['isLoading']),
  },
  created(){
    const vm = this;
    vm.getCart(); 
    vm.getAllProduct();
  },  
  methods:{ //  AJAXAPP mapGettersajax
    ...mapActions( 'cartsModules' , ['getCart'] ), //   APP
    ...mapActions( 'productsModules' , ['getAllProduct'] ), //    search Navbar  APP
  },

}
</script>

<style lang='scss'> 
@import './assets/all';

</style>