import Vue from 'vue';
import Vuex from 'vuex';
import store from './store';
import App from './App.vue';
import router from './router';
import axios from 'axios';
import VeeValidate from 'vee-validate'; // http://vee-validate.logaretm.com/v2/guide/rules.html#after 
import zhTW from 'vee-validate/dist/locale/zh_TW' ; // vee-validate  
import VueI18n from 'vue-i18n'; // vue-i18n
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import VueAxios from 'vue-axios' 
import 'bootstrap';
import './bus' ;
import currencyFilter from './fillters/currency';
import timeStampFilter from './fillters/unixFormat';

Vue.use(VueAxios, axios)
Vue.use(Vuex);
Vue.use(VueI18n); // i18n
const i18n = new VueI18n({ // VueI18n 
  locale: 'zhTW',
  silentFallbackWarn: true
});
Vue.use(VeeValidate, { // validate i18n
  i18n,
  dictionary: {
    zhTW , //vee-validate
    // silentTranslationWarn: process.env.NODE_ENV === 'production'
  }
});


Vue.config.productionTip = false

Vue.component('Loading',Loading); //  Vue.component loding

Vue.filter('currency',currencyFilter); // filter
Vue.filter('timeStamp',timeStampFilter); // UNIXfilter

axios.defaults.withCredentials = true; // session cookie
/* eslint-disable no-new */

new Vue({
  router,
  store,
  i18n, // i18n
  render: h => h(App)
}).$mount('#app')


router.beforeEach((to, from, next) => { // router.beforeEach((to, from, next)
    if(to.meta.requiresAuth){ // to to.meta.requiresAuth  true 
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const api = `${apiPath}/api/user/check` ; // api
      // API 
      //  API path
      axios.post(api).then((response) => { // vuerouter  this.$http.post  axios.post
        if(response.data.success){ //  next()
          next();
        }
        else{ // login
          next({
            path:'/login'
          })
        }
      }) 
    }
    else{ // to.meta.requiresAuth  true 
      next();
    }
  }
)