<template>
  <div>
    <!-- <loading :active.sync="isLoading" ></loading>         -->
    <div class="text-right">
      <button 
        class="btn btn-primary mt-4" 
        @click="openModal(true)" 
      >
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <th width="500"></th>
        <th ></th>
        <th ></th>
        <th ></th>
        <th ></th>
      </thead>
      <tbody>
        <tr 
          v-for="item in tickets" 
          :key="item.id"
        >
          <td>{{ item.title }}</td>
          <td>{{ item.percent }}</td>
          <td>{{ item.due_date | timeStamp }}</td>
          <td>
            <span 
              v-if="item.is_enabled" 
              class="text-success"
            >
              
            </span>
            <span v-else></span>
          </td>
          <td>
            <button 
              class="btn btn-outline-primary btn-sm"  
              @click="openModal(false,item)"
            >
              
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination 
      :pagination-prop="pagination" 
      @emit-update-page="getTickets"
    />      

    <!-- Modal -->
    <div 
      id="ticketModal" 
      class="modal fade" 
      tabindex="-1" 
      role="dialog"
      aria-labelledby="exampleModalLabel" 
      aria-hidden="true"
    >
      <div 
        class="modal-dialog modal-lg" 
        role="document"
      >
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 
              id="exampleModalLabel"
              class="modal-title" 
            >
              <span></span>
            </h5>
            <button 
              class="close" 
              type="button" 
              data-dismiss="modal" 
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-row">
              <div class="col-sm">
                <div class="form-group">
                  <label for="title"></label>
                  <input 
                    id="title"
                    v-model="newTicket.title"
                    class="form-control" 
                    type="text" 
                    placeholder=""
                  >
                </div>
                <div class="form-group ">
                  <label for="category"></label>
                  <input 
                    id="category"
                    v-model="newTicket.code"
                    class="form-control" 
                    type="text" 
                    placeholder=""
                  >
                </div>
                <div class="form-group ">
                  <label for="price"></label>
                  <input 
                    id="unit"
                    v-model="newTicket.due_date"
                    class="form-control" 
                    type="unit" 
                    placeholder="timeStamp"
                  >
                </div>    
                <div class="form-group ">
                  <label for="price"></label>
                  <input 
                    id="unit"
                    v-model="newTicket.percent"
                    type="unit" 
                    class="form-control" 
                    placeholder=""
                  >
                </div>     
                <div class="form-group">
                  <div class="form-check">
                    <input 
                      id="is_enabled"
                      :true-value="1"
                      :false-value="0"                                
                      v-model="newTicket.is_enabled"
                      class="form-check-input" 
                      type="checkbox"
                    >
                    <label 
                      class="form-check-label" 
                      for="is_enabled"
                    >
                      
                    </label>
                  </div>
                </div>                                                                             
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button 
              type="button" 
              class="btn btn-outline-secondary" 
              data-dismiss="modal"
            >
              
            </button>
            <button 
              class="btn btn-primary" 
              type="button" 
              @click="updateTicket"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>        
    <!-- Modal -->
    <!-- <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger">{{ newTicket.title }}</strong> ()
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-danger"
                ></button>
            </div>
            </div>
        </div>
    </div> -->
  </div>
</template>

<script>
import Pagination from '../../../components/Pagination' //Pagination 
import $ from 'jquery'; // jquery  ESLint $

export default {
  components:{
    Pagination
  },  
  data:function(){
    return{
      // isLoading:false,
      pagination:{}, // 
      newTicket:{
        percent:{
          default:100,
          type:Number
        }
      }, // timeStamp
      tickets:[], // 
      isNew:false, // 
    }
  },
  created(){
    this.getTickets();
  }, 
  methods:{
    getTickets(page = 1){ //    page 1  getProduct() pagepage1
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      const api = `${apiPath}/api/${customPath}/admin/coupons?page=${page}`; // Api + 
      vm.$store.dispatch('updateLoading',true);
      this.$http.get(api).then((response) => {
        console.log(response.data);
        vm.$store.dispatch('updateLoading',false);
        vm.tickets = response.data.coupons;  
        vm.pagination = response.data.pagination;
      })
    },        
    openModal(isNew,item){
      if(isNew){ // isNew data return  this.isNew
        this.newTicket ={}; //  this.newTicket  modal input 
        this.isNew = true ; //  isNew  true
      }
      else{
        // Object.assign({},item) => ES6 item  1item1
        this.newTicket =Object.assign({},item); // itemmodal this.newTicket item modal input 
        this.isNew = false ; //  isNew  false
      }
      $('#ticketModal').modal('show');
    },
    updateTicket(){
      const apiPath = process.env.VUE_APP_APIPATH ; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm =this ;
      let httpMethod = 'post' ;
      let api = `${apiPath}/api/${customPath}/admin/coupon` ; // api
      if(!vm.isNew){ //isNewfalse apiapi
        api = `${apiPath}/api/${customPath}/admin/coupon/${vm.newTicket.id}` ; // /api/:api_path/admin/product/:id id newTicket  id
        httpMethod = 'put' ; //  http put
      }
      this.$http[httpMethod](api,{data:vm.newTicket}).then((response) => { //   apidata  {data:vm.newTicket} 
        console.log(response.data);
        if(response.data.success){ // 
          console.log(response);
          $('#ticketModal').modal('hide'); // modal
          vm.getTickets() ;// 
          this.$bus.$emit('message:push',response.data.message,'success'); // 
        }
        else{
          console.log(response);
          $('#ticketModal').modal('hide'); // modal
          vm.getTickets() ;// 
          this.$bus.$emit('message:push',response.data.message,'danger'); // 
        }
      })
    }               
  },

}
</script>